<template>
  <div class="adM_wrap">
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        prop="name"
        label="广告名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="price"
        label="单价"
        width="180">
      </el-table-column>
      <el-table-column
        prop="os_type"
        label="投放终端">
      </el-table-column>
      <el-table-column
        prop="ad_link"
        label="投放地址">
      </el-table-column>
      <el-table-column
        prop="sts"
        label="投放状态">
      </el-table-column>
    </el-table>

    <!--分页-->
    <div class="block">
      <el-pagination
        @size-change=""
        @current-change="_init"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="totalNum">
      </el-pagination>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs'
  import {Utils, ERR_OK} from 'common/js/Utils'

  export default {
    data(){
      return {
        tableData: null,
        currentPage: 1,
        pageSize: 10,
        totalNum: null,

      }
    },
    computed: {
      getUserId(){
        return window.localStorage.getItem('nGGuser_id')
      },
      getToken(){
        return window.localStorage.getItem('nGGtoken')
      }
    },
    created(){
      this._init()
    },
    methods: {
      _init(){
        let params = {
          user_id: this.getUserId,
          token: this.getToken,
          page: this.currentPage,
          page_number: this.pageSize
        }
//        console.log(params)
        this.axios.post(Utils.adManage, qs.stringify(params))
          .then((response) => {
            if (response.data.code === ERR_OK) {
              this.totalNum = parseInt(response.data.data.count)
              this.tableData = response.data.data.advert_list
//              console.log(this.tableData)
            }
          })
      }

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .block
    margin: 10px
</style>



